import ols from './ols';
import { visitPoints } from './points';
import rSquared from './r-squared'; // Adapted from d3-regression by Harry Stevens
// License: https://github.com/HarryStevens/d3-regression/blob/master/LICENSE

export default function (data, x, y) {
  var X = 0,
      Y = 0,
      XY = 0,
      X2 = 0,
      n = 0;
  visitPoints(data, x, y, function (dx, dy) {
    X += dx;
    Y += dy;
    XY += dx * dy;
    X2 += dx * dx;
    ++n;
  });

  var coef = ols(X / n, Y / n, XY / n, X2 / n),
      predict = function predict(x) {
    return coef[0] + coef[1] * x;
  };

  return {
    coef: coef,
    predict: predict,
    rSquared: rSquared(data, x, y, Y / n, predict)
  };
}