export var Identity = 'identity';
export var Linear = 'linear';
export var Log = 'log';
export var Pow = 'pow';
export var Sqrt = 'sqrt';
export var Symlog = 'symlog';
export var Time = 'time';
export var UTC = 'utc';
export var Sequential = 'sequential';
export var Diverging = 'diverging';
export var Quantile = 'quantile';
export var Quantize = 'quantize';
export var Threshold = 'threshold';
export var Ordinal = 'ordinal';
export var Point = 'point';
export var Band = 'band';
export var BinOrdinal = 'bin-ordinal';
export function isValidScaleType(type) {
  switch (type) {
    case Identity:
    case Linear:
    case Log:
    case Pow:
    case Sqrt:
    case Symlog:
    case Time:
    case UTC:
    case Sequential:
    case Quantile:
    case Quantize:
    case Threshold:
    case Ordinal:
    case Point:
    case Band:
    case BinOrdinal:
      return true;
  }

  return false;
}
export function isTemporal(key) {
  return key === Time || key === UTC;
}
export function isBinned(key) {
  return key === BinOrdinal;
}
export function isQuantile(key) {
  return key === Quantile;
}
export function isSequential(key) {
  return key && key.startsWith(Sequential);
}
export function isDiverging(key) {
  return key && key.startsWith(Diverging);
}
export function isInterpolating(key) {
  return isSequential(key) || isDiverging(key);
}
export function isLogarithmic(key) {
  return key === Log || key.endsWith('-log');
}
export function isContinuous(key) {
  switch (key) {
    case Linear:
    case Log:
    case Pow:
    case Sqrt:
    case Symlog:
    case Time:
    case UTC:
    case Sequential:
      return true;
  }

  return false;
}
export function isDiscrete(key) {
  return key === BinOrdinal || key === Ordinal || key === Band || key === Point;
}
export function isDiscretizing(key) {
  return key === BinOrdinal || key === Quantile || key === Quantize || key === Threshold;
}