import { isArray } from 'vega-util';
import { COLOR, OPACITY } from '../../channel';
import { hasConditionalValueDef, isTimeFormatFieldDef, isValueDef } from '../../channeldef';
import { FILL_STROKE_CONFIG } from '../../mark';
import { ScaleType } from '../../scale';
import { getFirstDefined, keys } from '../../util';
import { applyMarkConfig, timeFormatExpression } from '../common';
import * as mixins from '../mark/mixins';
import { defaultType } from './properties';

function type(legendCmp, model, channel) {
  var scaleType = model.getScaleComponent(channel).get('type');
  return getFirstDefined(legendCmp.get('type'), defaultType({
    channel: channel,
    scaleType: scaleType,
    alwaysReturn: true
  }));
}

export function symbols(fieldDef, symbolsSpec, model, channel, legendCmp) {
  if (type(legendCmp, model, channel) !== 'symbol') {
    return undefined;
  }

  var out = Object.assign({}, applyMarkConfig({}, model, FILL_STROKE_CONFIG), mixins.color(model)); // FIXME: remove this when VgEncodeEntry is compatible with SymbolEncodeEntry

  var markDef = model.markDef,
      encoding = model.encoding,
      config = model.config;
  var filled = markDef.filled;
  var opacity = getMaxValue(encoding.opacity) || markDef.opacity;

  if (out.fill) {
    // for fill legend, we don't want any fill in symbol
    if (channel === 'fill' || filled && channel === COLOR) {
      delete out.fill;
    } else {
      if (out.fill['field']) {
        // For others, set fill to some opaque value (or nothing if a color is already set)
        if (legendCmp.get('symbolFillColor')) {
          delete out.fill;
        } else {
          out.fill = {
            value: config.legend.symbolBaseFillColor || 'black'
          };
          out.fillOpacity = {
            value: opacity || 1
          };
        }
      } else if (isArray(out.fill)) {
        var fill = getFirstConditionValue(encoding.fill || encoding.color) || markDef.fill || filled && markDef.color;

        if (fill) {
          out.fill = {
            value: fill
          };
        }
      }
    }
  }

  if (out.stroke) {
    if (channel === 'stroke' || !filled && channel === COLOR) {
      delete out.stroke;
    } else {
      if (out.stroke['field']) {
        // For others, remove stroke field
        delete out.stroke;
      } else if (isArray(out.stroke)) {
        var stroke = getFirstDefined(getFirstConditionValue(encoding.stroke || encoding.color), markDef.stroke, filled ? markDef.color : undefined);

        if (stroke) {
          out.stroke = {
            value: stroke
          };
        }
      }
    }
  }

  if (channel !== OPACITY) {
    if (opacity) {
      // only apply opacity if it is neither zero or undefined
      out.opacity = {
        value: opacity
      };
    }
  }

  out = Object.assign({}, out, symbolsSpec);
  return keys(out).length > 0 ? out : undefined;
}
export function gradient(fieldDef, gradientSpec, model, channel, legendCmp) {
  if (type(legendCmp, model, channel) !== 'gradient') {
    return undefined;
  }

  var out = {};
  var opacity = getMaxValue(model.encoding.opacity) || model.markDef.opacity;

  if (opacity) {
    // only apply opacity if it is neither zero or undefined
    out.opacity = {
      value: opacity
    };
  }

  out = Object.assign({}, out, gradientSpec);
  return keys(out).length > 0 ? out : undefined;
}
export function labels(fieldDef, labelsSpec, model, channel) {
  var legend = model.legend(channel);
  var config = model.config;
  var out = {};

  if (isTimeFormatFieldDef(fieldDef)) {
    var isUTCScale = model.getScaleComponent(channel).get('type') === ScaleType.UTC;
    var expr = timeFormatExpression('datum.value', fieldDef.timeUnit, legend.format, config.legend.shortTimeLabels, config.timeFormat, isUTCScale);
    labelsSpec = Object.assign({}, expr ? {
      text: {
        signal: expr
      }
    } : {}, labelsSpec);
  }

  out = Object.assign({}, out, labelsSpec);
  return keys(out).length > 0 ? out : undefined;
}

function getMaxValue(channelDef) {
  return getConditionValue(channelDef, function (v, conditionalDef) {
    return Math.max(v, conditionalDef.value);
  });
}

export function getFirstConditionValue(channelDef) {
  return getConditionValue(channelDef, function (v, conditionalDef) {
    return getFirstDefined(v, conditionalDef.value);
  });
}

function getConditionValue(channelDef, reducer) {
  if (hasConditionalValueDef(channelDef)) {
    return (isArray(channelDef.condition) ? channelDef.condition : [channelDef.condition]).reduce(reducer, channelDef.value);
  } else if (isValueDef(channelDef)) {
    return channelDef.value;
  }

  return undefined;
}