import { array8, array16, array32 } from './arrays';
/**
 * Maintains CrossFilter state.
 */

export default function Bitmaps() {
  var width = 8,
      _data = [],
      _seen = array32(0),
      _curr = array(0, width),
      _prev = array(0, width);

  return {
    data: function data() {
      return _data;
    },
    seen: function seen() {
      return _seen = lengthen(_seen, _data.length);
    },
    add: function add(array) {
      for (var i = 0, j = _data.length, n = array.length, t; i < n; ++i) {
        t = array[i];
        t._index = j++;

        _data.push(t);
      }
    },
    remove: function remove(num, map) {
      // map: index -> boolean (true => remove)
      var n = _data.length,
          copy = Array(n - num),
          reindex = _data,
          // reuse old data array for index map
      t,
          i,
          j; // seek forward to first removal

      for (i = 0; !map[i] && i < n; ++i) {
        copy[i] = _data[i];
        reindex[i] = i;
      } // condense arrays


      for (j = i; i < n; ++i) {
        t = _data[i];

        if (!map[i]) {
          reindex[i] = j;
          _curr[j] = _curr[i];
          _prev[j] = _prev[i];
          copy[j] = t;
          t._index = j++;
        } else {
          reindex[i] = -1;
        }

        _curr[i] = 0; // clear unused bits
      }

      _data = copy;
      return reindex;
    },
    size: function size() {
      return _data.length;
    },
    curr: function curr() {
      return _curr;
    },
    prev: function prev() {
      return _prev;
    },
    reset: function reset(k) {
      _prev[k] = _curr[k];
    },
    all: function all() {
      return width < 0x101 ? 0xff : width < 0x10001 ? 0xffff : 0xffffffff;
    },
    set: function set(k, one) {
      _curr[k] |= one;
    },
    clear: function clear(k, one) {
      _curr[k] &= ~one;
    },
    resize: function resize(n, m) {
      var k = _curr.length;

      if (n > k || m > width) {
        width = Math.max(m, width);
        _curr = array(n, width, _curr);
        _prev = array(n, width);
      }
    }
  };
}

function lengthen(array, length, copy) {
  if (array.length >= length) return array;
  copy = copy || new array.constructor(length);
  copy.set(array);
  return copy;
}

function array(n, m, array) {
  var copy = (m < 0x101 ? array8 : m < 0x10001 ? array16 : array32)(n);
  if (array) copy.set(array);
  return copy;
}